import { useEffect, useCallback, useState } from 'react';

import { Text, Button, Flex, useDisclosure } from '@wp-plugins/adapters';
import { __ } from '@wp-plugins/i18n';
import { useFormContext } from '@wp-plugins/form';

import { DataShape, useImportSettings, useData } from '../../services';

export const ImportSettings: React.FC = () => {
	const { isOpen: isActive, onOpen: onActivate } = useDisclosure();
	const [isImporting, setIsImporting] = useState(false);
	const { import_config } = useData('uiData');

	const importSettings = useImportSettings();

	useEffect(() => {
		// activate the button after licence modal is opened
		const timeout = setTimeout(onActivate, 3000);
		return () => clearTimeout(timeout);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);
	const { getValues } = useFormContext<DataShape>();

	const onClickImport = useCallback(async () => {
		const licence_key = getValues().advanced?.licence_key;

		await importSettings({ licence_key, setInProgress: setIsImporting });
	}, [getValues, importSettings]);

	return (
		<Flex alignItems='center' flexDirection='column' mb='2.5em'>
			<Text fontWeight='500'>{__('Do you want to import settings from WP Telegram (free version)?')}</Text>
			<Button
				size='lg'
				colorScheme='blue'
				my='1em'
				onClick={onClickImport}
				isLoading={!import_config.has_old_ver && !isActive}
				isDisabled={isImporting || import_config.has_old_ver}
			>
				{isImporting ? __('Please wait') : __('Import settings')}
			</Button>
			{import_config.has_old_ver && (
				<Text color='red.500' textAlign='center'>
					{__(
						'You seem to be on an older version of WP Telegram (free version). Please update it to the latest version, then import the settings.'
					)}
					<br />
					{!import_config.is_active && (
						<Text as='span' fontWeight='500'>
							{__('Make sure that it is active when updating.')}
						</Text>
					)}
				</Text>
			)}
			{import_config.is_active && (
				<Text fontWeight='500'>{__('You can deactivate the free version after importing settings.')}</Text>
			)}
		</Flex>
	);
};
