import { useCallback, useMemo } from 'react';

import { BaseApiUtilArgs, fetchAPI, getErrorMessage } from '@wp-plugins/services';

import { useData } from './useData';

interface LicenceUtilArgs extends BaseApiUtilArgs {
	licence_key?: string;
}

export type LicenceUtils = {
	checkLicence: (args: LicenceUtilArgs) => Promise<void>;
	activateLicence: (args: LicenceUtilArgs) => Promise<void>;
};

export const useLicenceUtils = (): LicenceUtils => {
	const { rest_namespace } = useData('api');

	const base_path = `${rest_namespace}/licence`;

	const sendLicenceRequest = useCallback(
		async (
			{ licence_key, setInProgress, setResult, setResultType }: LicenceUtilArgs,
			edd_action: 'activate' | 'check'
		): Promise<void> => {
			setInProgress?.(true);

			const path = `${base_path}/${edd_action}/`;
			const data = { licence_key };

			try {
				const result = await fetchAPI.POST({ path, data });

				setResultType?.('SUCCESS');
				setResult?.(result);
			} catch (error) {
				console.log('ERROR', error);
				setResultType?.('ERROR');
				setResult?.(getErrorMessage(error));
			} finally {
				setInProgress?.(false);
			}
		},
		[base_path]
	);

	const checkLicence = useCallback<LicenceUtils['checkLicence']>(
		async (args): Promise<void> => {
			await sendLicenceRequest(args, 'check');
		},
		[sendLicenceRequest]
	);

	const activateLicence = useCallback<LicenceUtils['activateLicence']>(
		async (args): Promise<void> => {
			await sendLicenceRequest(args, 'activate');
		},
		[sendLicenceRequest]
	);

	return useMemo(
		() => ({
			checkLicence,
			activateLicence,
		}),
		[activateLicence, checkLicence]
	);
};
