import { useState } from 'react';

import { Text } from '@wp-plugins/adapters';
import { __, sprintf } from '@wp-plugins/i18n';
import { Code } from '@wp-plugins/components';
import { createInterpolateElement } from '@wp-plugins/utilities';
import type { KeyboardButton } from '@wp-plugins/utilities';

import { DisplayButtons } from './DisplayButtons';
import { UrlButtonsInput } from './UrlButtonsInput';
import { PREFIX } from '../constants';
import { Heading } from './Heading';
import type { KeyboardButtonsProps } from './types';

const arrayName = `${PREFIX}.buttons` as const;

const DEFAULT_URL_BUTTON: KeyboardButton = {
	label: '',
	url: '',
};

export const UrlButtons: React.FC<KeyboardButtonsProps> = ({ buttonsArray }) => {
	const [btnInputValues, setBtnInputValues] = useState<KeyboardButton>(DEFAULT_URL_BUTTON);

	return (
		<>
			<Heading>{__('URL Buttons')}</Heading>
			<DisplayButtons
				btnInputValues={btnInputValues}
				setBtnInputValues={setBtnInputValues}
				name={arrayName}
				{...buttonsArray}
				asUrlButtons
			/>
			<UrlButtonsInput
				btnInputValues={btnInputValues}
				defaultInputValues={DEFAULT_URL_BUTTON}
				setBtnInputValues={setBtnInputValues}
				name={arrayName}
				{...buttonsArray}
			/>
			<Text>
				{__(
					'In URL field, you can set an absolute URL or a dynamic one using the custom field tags as in Message Template.'
				)}
			</Text>
			<Text>
				{createInterpolateElement(sprintf(__('For example %s'), '<Ex />'), {
					Ex: (
						<>
							<Code>{'https://domain.com'}</Code>,<Code>{'{post_url}'}</Code>,<Code>{'{home_url}'}</Code>,
							<Code>{'{cf:affiliate_url}'}</Code>,<Code>{'{acf:_amzn_url}'}</Code>
						</>
					),
				})}
			</Text>
			<Text>
				<Text color='red.400' as='span'>
					{__('Note:')}
				</Text>
				&nbsp;
				{createInterpolateElement(
					sprintf(
						__('If you add the tags as URL query parameters, make sure you wrap them in %s.'),
						`<Tag />`
					),
					{
						Tag: <Code>{'{encode:<tag>}'}</Code>,
					}
				)}
			</Text>
			<Text>
				{createInterpolateElement(sprintf(__('For example %s'), '<Ex />'), {
					Ex: (
						<>
							<Code>{'{home_url}?utm_source={encode:{post_title}}'}</Code>,
							<Code>{'https://t.me/share/url?url={encode:{post_url}}&text={encode:{post_title}}'}</Code>
						</>
					),
				})}
			</Text>
		</>
	);
};
