import { useEffect, useMemo } from 'react';
import { findIndex, pathEq } from 'ramda';

import { __, sprintf } from '@wp-plugins/i18n';
import { FormFieldProps, useFormContext } from '@wp-plugins/form';
import { useBotTokenTest } from '@wp-plugins/components';
import type { TestResult, TestResultType } from '@wp-plugins/services';

import { getFieldLabel, DataShape } from '../../services';

const is_https = 'https:' === window.location.protocol;

type BotFields = {
	fields: FormFieldProps<any, any>['subFields'];
	testResult: TestResult;
	testResultType: TestResultType;
};

export const useBotFields = (): BotFields => {
	const { bot_token, bot_username, buttonComponent, testResult, testResultType } = useBotTokenTest();
	const { getValues, setValue, trigger } = useFormContext<DataShape>();

	useEffect(() => {
		if (!bot_username) {
			return;
		}
		const collection = getValues()?.bots?.collection || [];
		const index = findIndex(pathEq(['value', 'bot_token'], bot_token), collection);

		const usernamePath = `bots.collection.${index}.value.bot_username` as const;

		setValue(usernamePath, bot_username as never);
		trigger(usernamePath);
	}, [bot_token, bot_username, getValues, setValue, trigger]);

	return useMemo(
		() => ({
			fields: [
				{
					name: 'bot_token',
					fieldType: 'text.button',
					label: getFieldLabel('bot_token'),
					button: buttonComponent,
					description: __('Please read the instructions above.'),
				},
				{
					name: 'bot_username',
					fieldType: 'text',
					label: getFieldLabel('bot_username'),
					addonBefore: '@',
					description: sprintf(__('Use %s to set automatically.'), __('Test Token')),
				},
				{
					name: 'update_method',
					label: getFieldLabel('update_method'),
					fieldType: 'select',
					options: [
						{
							value: 'webhook',
							label: __('Webhook') + ` (${is_https ? __('Recommended') : __('Requires SSL')})`,
							disabled: !is_https,
						},
						{
							value: 'long_polling',
							label: __('Long Polling'),
						},
						{
							value: 'none',
							label: __('None'),
						},
					],
				},
			],
			testResult,
			testResultType,
		}),
		[buttonComponent, testResult, testResultType]
	);
};
