import { useMemo } from 'react';

import type { OptionsType } from '@wp-plugins/adapters';

import { useBotCollection } from './useBotCollection';

export const useBotOptions = (emptyLabel = ''): OptionsType => {
	const collection = useBotCollection();

	return useMemo(() => {
		const options = collection
			.map(({ value }) => {
				if (value?.bot_username) {
					return {
						value: value?.bot_username,
						label: `@${value?.bot_username}`,
					};
				}
				return null;
			})
			.filter(Boolean); // remove empty values

		if (emptyLabel) {
			options.unshift({
				value: '',
				label: emptyLabel,
			});
		}

		return options;
	}, [collection, emptyLabel]);
};
