import { __ } from '@wp-plugins/i18n';
import { Link } from '@wp-plugins/adapters';
import { FormField, FormFieldProps } from '@wp-plugins/form';
import { prefixName } from '@wp-plugins/utilities';

import { getFieldLabel } from '../services';
import type { CommonProps } from './types';

export type ParseModeFieldProps = Partial<FormFieldProps<any, any>> & CommonProps;

export const getParseModeOptions = () => [
	{
		value: 'none',
		label: __('None'),
	},
	{
		value: 'HTML',
		label: __('HTML style'),
	},
];

export const ParseModeField: React.FC<ParseModeFieldProps> = ({ prefix, ...props }) => {
	return (
		<FormField
			name={prefixName('parse_mode', prefix)}
			fieldType='radio'
			label={getFieldLabel('parse_mode')}
			after={
				<Link
					color='blue.500'
					href='https://core.telegram.org/bots/api#html-style'
					isExternal
					mt='0.5rem'
					display='block'
				>
					{__('Learn more')}
				</Link>
			}
			options={getParseModeOptions()}
			{...props}
		/>
	);
};
