import * as yup from 'yup';

import { sprintf, __ } from '@wp-plugins/i18n';
import {
	fieldLabelGetter,
	getFormErrorMessage,
	BOT_TOKEN_REGEX,
	TG_USERNAME_REGEX,
	BotInstance,
	TG_PRIVATE_CHAT_ID_REGEX,
} from '@wp-plugins/utilities';
import {
	proxySchema,
	fieldLabels as sharedFieldLabels,
	parseModeSchema,
} from '@wp-plugins/wptelegram-shared--core-pro';

export const fieldLabels = {
	...sharedFieldLabels,
	channels: () => __('Channel(s)'),
	chat_id: () => __('Chat ID'),
	email_goes_to: () => __('If Email goes to'),
	licence_key: (): string => __('Licence key'),
	send_attachments: () => __('Send attachments'),
	send_to_chat: (): string => __('Send it to'),
	update_method: () => __('Update method'),
};

export const getFieldLabel = fieldLabelGetter(fieldLabels);

const botTokenSchema = (field: keyof typeof fieldLabels) =>
	yup
		.string()
		.trim()
		.matches(BOT_TOKEN_REGEX, {
			message: () => getErrorMessage(field, 'invalid'),
			excludeEmptyString: true,
		})
		.when('active', (active: boolean, schema: ReturnType<typeof yup.string>) =>
			!active ? schema : schema.required(() => getErrorMessage(field, 'required'))
		);

const botUsernameSchema = (field: keyof typeof fieldLabels) =>
	yup
		.string()
		.trim()
		.matches(TG_USERNAME_REGEX, {
			message: () => getErrorMessage(field, 'invalid'),
			excludeEmptyString: true,
		})
		.when('active', (active: boolean, schema: ReturnType<typeof yup.string>) =>
			!active ? schema : schema.required(() => getErrorMessage(field, 'required'))
		);

export const validationSchema = yup.object({
	bots: yup.object({
		collection: yup
			.array()
			.required(sprintf(__('At least one %s is required.'), __('bot')))
			.of(
				yup.object({
					value: yup.object({
						bot_token: botTokenSchema('bot_token'),
						bot_username: botUsernameSchema('bot_username'),
						update_method: yup
							.string()
							.oneOf<BotInstance['update_method']>(['webhook', 'long_polling', 'none'], () =>
								getErrorMessage('update_method', 'invalid')
							),
					}),
				})
			),
	}),
	p2tg: yup.object({
		bot: botUsernameSchema('bot'),
		plugin_posts: yup.boolean(),
		post_edit_switch: yup.boolean(),
	}),
	notify: yup.object({
		bot: botUsernameSchema('bot'),
		catch_emails: yup.array().of(
			yup.object({
				value: yup.object({
					email: yup.string(),
					chat_ids: yup
						.array()
						.compact((field) => !field.value)
						.of(
							yup.object({
								value: yup
									.string()
									.trim()
									// match private chat ID.
									.matches(TG_PRIVATE_CHAT_ID_REGEX, {
										message: () => getErrorMessage('chat_id', 'invalid'),
										excludeEmptyString: true,
									}),
							})
						),
				}),
			})
		),
		message_template: yup.string(),
		parse_mode: parseModeSchema,
	}),
	proxy: proxySchema,
	advanced: yup.object(),
});

export const getErrorMessage = getFormErrorMessage(fieldLabels);
