import { pathOr } from 'ramda';

import { Box, Divider } from '@wp-plugins/adapters';
import { RenderTestResult } from '@wp-plugins/components';
import { AfterRowProps, useFormContext, useWatch, useFieldError } from '@wp-plugins/form';
import type { TestResult, TestResultType } from '@wp-plugins/services';
import { strToPath } from '@wp-plugins/utilities';

import { WebhookInfo } from './WebhookInfo';
import { DEAFULT_BOT_INSTANCE } from './constants';

interface AfterBotRowProps extends AfterRowProps {
	testResult: TestResult;
	testResultType: TestResultType;
}

export const AfterBotRow: React.FC<AfterBotRowProps> = ({
	testResult: botTokenTestResult,
	testResultType: botTokenTestResultType,
	name,
}) => {
	const { getValues } = useFormContext();
	const error = useFieldError(name);

	const defaultValue = pathOr(DEAFULT_BOT_INSTANCE, strToPath(name), getValues());

	const { bot_token, update_method } = useWatch({ name, defaultValue });

	let testResult: React.ReactNode, webhookInfo: React.ReactNode;

	if (bot_token) {
		const result = botTokenTestResult[bot_token];
		const resultType = botTokenTestResultType[bot_token];
		testResult = result && <RenderTestResult result={result} resultType={resultType} />;

		if ('webhook' === update_method) {
			webhookInfo = <WebhookInfo bot_token={bot_token} hasError={Boolean(error)} />;
		}
	}
	if (!testResult && !webhookInfo) {
		return null;
	}

	return (
		<Box>
			{testResult ? <Box>{testResult}</Box> : null}
			{testResult && webhookInfo ? <Divider my='0.5em' /> : null}
			{webhookInfo ? <Box>{webhookInfo}</Box> : null}
		</Box>
	);
};
