import { useMemo, useState, useEffect } from 'react';

import { __ } from '@wp-plugins/i18n';
import { useLocalStorage, ResultType } from '@wp-plugins/services';

import { useLicenceUtils, getLicenceErrorMessage, useData } from '../../../services';

export type LicenceInfo = {
	key: string;
	error?: string;
	requestStatus?: ResultType;
	status?: string;
	lastActivated?: [year: number, month: number, day: number];
};

const TODAY = new Date();

export const useLicenceInfo = (key: string): LicenceInfo => {
	const { name } = useData('pluginInfo');
	const { getItem, setItem } = useLocalStorage(name, {});

	const [checkResult, setResult] = useState<any>();
	const [checkResultType, setResultType] = useState<ResultType>();

	const { lastActivated = [0, 0, 0], key: activatedKey, status } = getItem<LicenceInfo>('licenceInfo', { key: '' });

	const lastCheckDate = new Date(...lastActivated);
	const alreadyCheckedToday = activatedKey === key && TODAY.getTime() - lastCheckDate.getTime() < 86400000;

	const { checkLicence } = useLicenceUtils();

	useEffect(() => {
		if (!key || alreadyCheckedToday) {
			return;
		}

		setResult(null);
		setResultType(null);
		checkLicence({
			licence_key: key,
			setResult: setResult,
			setResultType: setResultType,
		});
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	useEffect(() => {
		if (checkResult && true === checkResult.success && 'valid' === checkResult.license) {
			setItem('licenceInfo', {
				key,
				lastActivated: [TODAY.getFullYear(), TODAY.getMonth(), TODAY.getDate()],
				status: checkResult.license,
			});
		}
	}, [activatedKey, checkResult, key, setItem, status]);

	let error = alreadyCheckedToday ? '' : key || 'empty';

	if ('SUCCESS' === checkResultType) {
		if (true === checkResult?.success && 'valid' === checkResult?.license) {
			error = '';
		} else {
			error = getLicenceErrorMessage(checkResult?.license, checkResult);
		}
	} else if ('ERROR' === checkResultType) {
		error = __('Could not connect') === checkResult ? '' : checkResult;
	}
	return useMemo(
		() => ({ key, error, requestStatus: checkResultType, status: checkResult?.license }),
		[checkResult?.license, key, error, checkResultType]
	);
};
