import { useCallback } from 'react';

import { Box, Flex } from '@wp-plugins/adapters';
import { uuid } from '@wp-plugins/utilities';
import { __ } from '@wp-plugins/i18n';
import { FormField } from '@wp-plugins/form';
import { KeyboardButton } from '@wp-plugins/wptelegram-shared--core-pro';
import type { KeyboardButton as TKeyboardButton } from '@wp-plugins/utilities';

import type { KeyboardButtonsArray } from './types';
import { canButtonBeModified } from './utils';

export const DisplayButtons: React.FC<KeyboardButtonsArray> = ({
	name,
	asUrlButtons = false,
	setBtnInputValues,
	fields,
	remove,
}) => {
	const onDelete = useCallback(
		(field: TKeyboardButton, index: number) => () => {
			canButtonBeModified(field) && remove(index);
		},
		[remove]
	);

	const onEdit = useCallback(
		(field: TKeyboardButton) => () => {
			canButtonBeModified(field) && setBtnInputValues(field);
		},
		[setBtnInputValues]
	);

	const onCopy = useCallback(
		(field: TKeyboardButton) => () => {
			setBtnInputValues({
				...field,
				id: uuid(),
				// @ts-ignore
				btnId: uuid(),
			});
		},
		[setBtnInputValues]
	);

	return (
		<Flex alignItems='center' justifyContent='center' flexWrap='wrap'>
			{fields
				.map((field, index) => {
					const { btnId, value, label, url } = field;
					const isUrlButton = Boolean(url);

					if ((asUrlButtons && !isUrlButton) || (!asUrlButtons && isUrlButton)) {
						return null;
					}

					const buttonName = `${name}.${index}`;

					return (
						<Box key={btnId} mb='1em'>
							{/* Register hidden fields to maintain ref */}
							{isUrlButton ? (
								<FormField name={`${buttonName}.url`} defaultValue={url} fieldType='hidden' />
							) : (
								<FormField name={`${buttonName}.value`} defaultValue={value} fieldType='hidden' />
							)}
							<FormField name={`${buttonName}.label`} defaultValue={label} fieldType='hidden' />

							<KeyboardButton
								aria-label={__('Remove button')}
								button={field}
								onDelete={onDelete(field, index)}
								onEdit={onEdit(field)}
								onCopy={onCopy(field)}
								// isDisabled={DEFAULT_BUTTONS.includes(value)}
							/>
						</Box>
					);
				})
				.filter(Boolean)}
		</Flex>
	);
};
