import { useState, useCallback } from 'react';

import { Box, Button, TextInput, Flex, Text, Link, Modal as ModalComponent, FormControl } from '@wp-plugins/adapters';
import { __, sprintf } from '@wp-plugins/i18n';
import { useFormContext } from '@wp-plugins/form';
import type { ResultType } from '@wp-plugins/services';

import { DataShape, useLicenceUtils } from '../../../services';
import type { LicenceInfo } from './useLicenceInfo';
import ActivationResult from './ActivationResult';
import { PREFIX } from '../constants';

import './style.scss';

interface ModalProps {
	licenceInfo: LicenceInfo;
	onClose: VoidFunction;
	isOpen: boolean;
	title?: string;
}

const titleFontSize = ['1.4rem', '2rem'];
const wrapperProps = { alignItems: 'center' };
const addonAfterProps = { height: 'auto', px: '0' };

export const Modal: React.FC<ModalProps> = ({ licenceInfo, isOpen, onClose, title }) => {
	const [isActivating, setIsActivating] = useState(false);
	const [activationResult, setActivationResult] = useState();
	const [activationResultType, setActivationResultType] = useState<ResultType>();

	const { getValues, setValue } = useFormContext<DataShape>();

	const defaultLicenceKey = getValues()?.advanced?.licence_key;
	const [inputValue, setInputValue] = useState(defaultLicenceKey);

	const onChange = useCallback(
		(event: React.ChangeEvent<HTMLInputElement>) => {
			setValue(`${PREFIX}.licence_key` as const, event.target.value);
			setInputValue(event.target.value);
		},
		[setValue]
	);

	const { activateLicence } = useLicenceUtils();

	const onClickActivate = useCallback(async () => {
		setActivationResult(undefined);
		setActivationResultType(undefined);
		await activateLicence({
			licence_key: inputValue,
			setInProgress: setIsActivating,
			setResult: setActivationResult,
			setResultType: setActivationResultType,
		});
	}, [activateLicence, inputValue]);

	const button = (
		<Button
			isDisabled={!inputValue || isActivating}
			onClick={onClickActivate}
			colorScheme='blue'
			borderStartRadius='0'
		>
			{isActivating ? __('Please wait') : __('Activate')}
		</Button>
	);

	return (
		<ModalComponent
			isOpen={isOpen}
			onClose={onClose}
			closeOnEsc={false}
			closeOnOverlayClick={false}
			// isClosable={false}
			className='licence-activation'
			bodyClassName='licence-activation__body'
		>
			<Flex flexDirection='column' alignItems='center' justifyContent='center'>
				<Box mb='1em'>
					<Text as='h3' fontSize={titleFontSize} lineHeight='1.25' textAlign='center' my='1em'>
						{title}
					</Text>
					<Text textAlign='center'>
						<Text as='span'>
							{sprintf(__('Please goto %s page to find the licence key.'), __('My Account'))}
						</Text>
						&nbsp;
						<Text as='span'>{__('Or')}</Text>&nbsp;
						<Link href='https://wpsocio.com/my-account/' className='link' isExternal>
							{__('Click here')}
						</Link>
					</Text>
				</Box>

				<FormControl className='form-control licence-key'>
					<Box className='form-control__input'>
						<TextInput
							wrapperProps={wrapperProps}
							addonAfterProps={addonAfterProps}
							defaultValue={defaultLicenceKey}
							addonAfter={button}
							onChange={onChange}
						/>
						<Text mt='1em'>{__('Licence key helps you receive the plugin updates automatically.')}</Text>
					</Box>
				</FormControl>
				{licenceInfo?.key ? (
					<ActivationResult activationResult={activationResult} activationResultType={activationResultType} />
				) : null}
			</Flex>
		</ModalComponent>
	);
};
