import { Description, SectionCard } from '@wp-plugins/components';
import { __, sprintf } from '@wp-plugins/i18n';
import { FormField } from '@wp-plugins/form';
import { Divider, Text } from '@wp-plugins/adapters';

import { ActiveField, NotifyInstructions, UserNotifications, IfActive } from '@wp-plugins/wptelegram-shared--core-pro';

import { getFieldLabel, useBotOptions, useData } from '../../services';
import { PREFIX } from './constants';
import { IfBot } from '../shared/IfBot';
import { MessageSettings } from './MessageSettings';
import { CatchEmails } from './CatchEmails';

import './styles.scss';

export const Notify: React.FC = () => {
	const botOptions = useBotOptions();
	const { editProfileUrl } = useData('assets');

	return (
		<>
			<Description>
				{__(
					'The module will watch the Email Notifications sent from this site and deliver them to you on Telegram.'
				)}
			</Description>
			<ActiveField prefix={PREFIX} />

			<IfActive name={`${PREFIX}.active`}>
				<>
					<FormField
						name={`${PREFIX}.bot`}
						fieldType='select'
						label={getFieldLabel('bot')}
						description={sprintf(
							// translators: %s is module name
							__('Default bot for %s module.'),
							__('Post to Telegram')
						)}
						options={botOptions}
					/>
					<IfBot name={`${PREFIX}.bot`}>
						{(botUsername) => (
							<>
								<NotifyInstructions botUsername={botUsername} videoId='UtXDm9SEoRA' />
								<SectionCard title={__('Email to Telegram')}>
									<Text fontWeight='500' my='0.5em'>
										{__(
											'Any email notification that goes to the below entered email addresses will be sent to the corresponding chat on Telegram.'
										)}
									</Text>

									<CatchEmails botUsername={botUsername} />
								</SectionCard>
								<Divider />
								<UserNotifications editProfileUrl={editProfileUrl} prefix={PREFIX} />
								<Divider />
								<MessageSettings />
								<FormField
									name={`${PREFIX}.send_attachments`}
									fieldType='switch'
									label={getFieldLabel('send_attachments')}
									description={__('Forward email attachments along with text.')}
								/>
							</>
						)}
					</IfBot>
				</>
			</IfActive>
		</>
	);
};
