import { usePluginData } from '@wp-plugins/services';
import type { WPTelegramProData } from './types';

export const useData = <K extends keyof WPTelegramProData | undefined = undefined>(
	key?: K
): K extends undefined ? WPTelegramProData : WPTelegramProData[K] => {
	return usePluginData('wptelegram_pro', key);
};
