import { FormField } from '@wp-plugins/form';
import { __ } from '@wp-plugins/i18n';

import { DEAFAULT_CATCH_EMAILS, PREFIX } from './constants';
import { useCatchEmailsFields } from './useCatchEmailsFields';
import { AfterCatchEmails } from './AfterCatchEmails';

const defaultItem = { value: DEAFAULT_CATCH_EMAILS };

export const CatchEmails: React.FC<{ botUsername: string }> = ({ botUsername }) => {
	const { fields: subFields, testResult, testResultType } = useCatchEmailsFields(botUsername);

	return (
		<FormField
			name={`${PREFIX}.catch_emails`}
			fieldType='group'
			initialLabel={__('Entries')}
			isRepeatable
			renderAsSection
			defaultItem={defaultItem}
			groupClassName='catch-emails-group'
			subFields={subFields}
			// eslint-disable-next-line react-perf/jsx-no-new-function-as-prop
			afterRow={(props) => (
				<AfterCatchEmails {...props} testResult={testResult} testResultType={testResultType} />
			)}
		/>
	);
};
