import { Description } from '@wp-plugins/components';
import { __, sprintf } from '@wp-plugins/i18n';
import { FormField } from '@wp-plugins/form';

import { ActiveField, IfActive } from '@wp-plugins/wptelegram-shared--core-pro';

import { getFieldLabel, useBotOptions } from '../../services';
import { PREFIX } from './constants';
import { IfBot } from '../shared/IfBot';
import { Fields } from './Fields';

export const P2TG: React.FC = () => {
	const botOptions = useBotOptions();

	return (
		<>
			<Description>{__('With this module, you can configure how the posts are sent to Telegram.')}</Description>

			<ActiveField prefix={PREFIX} />

			<IfActive name={`${PREFIX}.active`}>
				<>
					<FormField
						name={`${PREFIX}.bot`}
						fieldType='select'
						label={getFieldLabel('bot')}
						description={sprintf(
							// translators: %s is module name
							__('Default bot for %s module.'),
							__('Post to Telegram')
						)}
						options={botOptions}
					/>
					<IfBot name={`${PREFIX}.bot`}>
						<Fields />
					</IfBot>
				</>
			</IfActive>
		</>
	);
};
