import { useCallback } from 'react';

import { FormField, useWatch } from '@wp-plugins/form';
import { __ } from '@wp-plugins/i18n';

import { useData, DataShape } from '../../services';
import { Instructions } from './Instructions';
import { ImportSettings } from '../importSettings';
import { PREFIX, DEAFULT_BOT_INSTANCE } from './constants';
import { useBotFields } from './useBotFields';
import { AfterBotRow } from './AfterBotRow';

import './styles.scss';

const defaultItem = { value: DEAFULT_BOT_INSTANCE };

export const Bots: React.FC = () => {
	const { import_config } = useData('uiData');

	const { fields: subFields, testResult, testResultType } = useBotFields();

	const bots = useWatch<DataShape, `${typeof PREFIX}.collection`>({ name: `${PREFIX}.collection` as const });

	const repeatableItemLabel = useCallback(
		(index: number) => {
			const username = bots?.[index]?.value?.bot_username;
			return username ? `@${username}` : '';
		},
		[bots]
	);

	return (
		<>
			<Instructions />
			{import_config.show_import && <ImportSettings />}

			<FormField
				name={`${PREFIX}.collection`}
				fieldType='group'
				isRepeatable
				repeatableItemLabel={repeatableItemLabel}
				defaultItem={defaultItem}
				groupClassName='bot-group'
				subFields={subFields}
				renderAsSection
				addButtonLabel={__('Add Bot')}
				// eslint-disable-next-line react-perf/jsx-no-new-function-as-prop
				afterRow={(props) => <AfterBotRow {...props} testResult={testResult} testResultType={testResultType} />}
			/>
		</>
	);
};
