import { useMemo } from 'react';
import { pathOr, pick, isEmpty } from 'ramda';

import { MessageResult } from '@wp-plugins/components';
import { AfterRowProps, useFormContext, useWatch } from '@wp-plugins/form';
import type { TestResult, TestResultType } from '@wp-plugins/services';
import { strToPath } from '@wp-plugins/utilities';

import { DEAFAULT_CATCH_EMAILS } from './constants';

interface AfterCatchEmailsProps extends AfterRowProps {
	testResult: TestResult;
	testResultType: TestResultType;
}

export const AfterCatchEmails: React.FC<AfterCatchEmailsProps> = ({ testResult, testResultType, name }) => {
	const { getValues } = useFormContext();

	const defaultValue = pathOr(DEAFAULT_CATCH_EMAILS, strToPath(name), getValues());

	const catchEmail = useWatch({ name, defaultValue });

	const chatIds = catchEmail?.chat_ids?.map(({ value }) => value);

	const messageResult = useMemo(() => pick(chatIds || [], testResult), [chatIds, testResult]);

	return (
		chatIds?.length > 0 &&
		!isEmpty(messageResult) && <MessageResult messageResult={messageResult} messageResultType={testResultType} />
	);
};
