import { useEffect, useMemo } from 'react';

import { __ } from '@wp-plugins/i18n';
import { useWatch, useFormContext } from '@wp-plugins/form';

import { AdvancedSettings as SharedAdvancedSettings } from '@wp-plugins/wptelegram-shared--core-pro';

import { useData } from '../../services';
import { PREFIX } from './constants';
import { Licence } from './licence';

export const Advanced: React.FC = () => {
	const { assets, uiData } = useData();
	const enable_logs = useWatch({ name: `${PREFIX}.enable_logs` });
	const { setValue } = useFormContext();

	useEffect(() => {
		if (enable_logs?.includes('p2tg') && !enable_logs?.includes('bot_api_out')) {
			setValue(`${PREFIX}.enable_logs`, [...enable_logs, 'bot_api_out']);
		}
	}, [enable_logs, setValue]);

	const log_options = useMemo(
		() => [
			{
				value: 'bot_api_out',
				label: __('Bot API') + ' ' + __('(Outgoing Requests)'),
				viewLink: assets.botApiLogUrl,
				isDisabled: enable_logs?.includes('p2tg'),
			},
			{
				value: 'bot_api_in',
				label: __('Bot API') + ' ' + __('(Incoming Requests)'),
				viewLink: assets.botUpdatesLogUrl,
			},
			{
				value: 'p2tg',
				label: __('Post to Telegram'),
				viewLink: assets.p2tgLogUrl,
			},
		],
		[assets, enable_logs]
	);

	return (
		<>
			<SharedAdvancedSettings prefix={PREFIX} log_options={log_options} debug_info={uiData?.debug_info} />
			<Licence />
		</>
	);
};
