import { useState } from 'react';

import { Text } from '@wp-plugins/adapters';
import { __ } from '@wp-plugins/i18n';
import type { KeyboardButton } from '@wp-plugins/utilities';

import { DisplayButtons } from './DisplayButtons';
import { ReactionButtonsInput } from './ReactionButtonsInput';
import { PREFIX } from '../constants';
import { Heading } from './Heading';
import type { KeyboardButtonsProps } from './types';

const arrayName = `${PREFIX}.buttons` as const;

const DEFAULT_RXN_BUTTON: KeyboardButton = {
	label: '',
	value: '',
};

export const ReactionButtons: React.FC<KeyboardButtonsProps> = ({ buttonsArray }) => {
	const [btnInputValues, setBtnInputValues] = useState<KeyboardButton>(DEFAULT_RXN_BUTTON);

	return (
		<>
			<Heading>{__('Reaction Buttons')}</Heading>
			<DisplayButtons
				btnInputValues={btnInputValues}
				setBtnInputValues={setBtnInputValues}
				name={arrayName}
				{...buttonsArray}
			/>
			<ReactionButtonsInput
				name={arrayName}
				btnInputValues={btnInputValues}
				defaultInputValues={DEFAULT_RXN_BUTTON}
				setBtnInputValues={setBtnInputValues}
				{...buttonsArray}
			/>

			<Text>
				<Text as='span' fontWeight='bold'>
					{__('Note:')}&nbsp;
				</Text>
				<Text as='em'>
					{__('Button name should be unique and contain only a maximum of 8 English letters or underscore.')}
				</Text>
			</Text>
		</>
	);
};
