import { useFormContext, useWatch } from '@wp-plugins/form';

import type { DataShape } from './types';
import { useData } from './useData';
import { prepDefaultValues } from './utils';

const name = 'bots.collection' as const;

const DEFAULT_COLLECTION = [];

export const useBotCollection = (): DataShape['bots']['collection'] => {
	const { getValues } = useFormContext<DataShape>();
	const bots = prepDefaultValues(useData('savedSettings'))?.bots;

	return useWatch<DataShape, 'bots.collection'>({
		name,
		defaultValue: getValues()?.bots?.collection || bots?.collection || DEFAULT_COLLECTION,
	});
};
