import { useState, useCallback } from 'react';
import { findIndex, propEq } from 'ramda';

import { __ } from '@wp-plugins/i18n';
import { CheckIcon } from '@wp-plugins/icons';
import { Flex, IconButton } from '@wp-plugins/adapters';
import { uuid } from '@wp-plugins/utilities';

import ButtonInput from './ButtonInput';
import type { ButtonInputProps } from './types';
import { canButtonBeModified } from './utils';

const getInputs = () => [
	{
		name: 'value',
		placeholder: 'name',
		style: { width: '40%' },
	},
	{
		name: 'label',
		placeholder: __('Label') + ' ',
	},
];

export const ReactionButtonsInput: React.FC<ButtonInputProps> = ({
	fields,
	append,
	update,
	defaultInputValues,
	btnInputValues,
	setBtnInputValues,
}) => {
	const [isRxnBtnInvalid, setIsRxnBtnInvalid] = useState(false);

	const isDisabled = !(btnInputValues.label && btnInputValues.value);

	const submit = useCallback(() => {
		const value = btnInputValues.value.trim();
		let inValid = true;
		if (/^[a-z_]{1,8}$/i.test(value) && canButtonBeModified(btnInputValues)) {
			const btnExists = fields.some((btn) => btn.value === value);
			const isNew = !('id' in btnInputValues);

			inValid = btnExists && isNew;

			if (!inValid) {
				const label = btnInputValues.label.trim();
				if (isNew) {
					append({ id: uuid(), ...btnInputValues, value, label });
				} else {
					const index = findIndex(propEq<string>('id', btnInputValues.id), fields);
					if (index < 0) {
						append({ ...btnInputValues, value, label });
					} else {
						update(index, { ...btnInputValues, value, label });
					}
				}
				setBtnInputValues(defaultInputValues);
			}
		}
		setIsRxnBtnInvalid(inValid);
	}, [btnInputValues, fields, append, setBtnInputValues, defaultInputValues, update]);

	const onChangeValue = useCallback(
		(name: string) => (value: string) => {
			setBtnInputValues((prevState) => ({
				...prevState,
				[name]: value,
			}));
		},
		[setBtnInputValues]
	);

	return (
		<Flex maxWidth='400px' border={isRxnBtnInvalid ? '1px solid red' : null} m='0 auto' my='1em'>
			{getInputs().map((input) => {
				const { name, style, ...rest } = input;
				return (
					<ButtonInput
						key={name}
						value={btnInputValues[name]}
						onPressEnter={!isDisabled && submit}
						onChangeValue={onChangeValue(name)}
						style={style}
						maxLength={'value' === name ? 8 : null}
						{...rest}
					/>
				);
			})}
			<IconButton
				isDisabled={isDisabled}
				onClick={submit}
				icon={<CheckIcon />}
				borderRadius={0}
				aria-label={__('Done')}
				minW='2.5rem'
			/>
		</Flex>
	);
};
