import { useCallback } from 'react';

import { BaseApiUtilArgs, fetchAPI, useDisplayFeedback } from '@wp-plugins/services';
import { __ } from '@wp-plugins/i18n';

import { useData } from './useData';

interface ImportSettingsArgs extends BaseApiUtilArgs {
	licence_key?: string;
}

export type ImportSettings = (args: ImportSettingsArgs) => Promise<void>;

export const useImportSettings = (): ImportSettings => {
	const { rest_namespace } = useData('api');
	const { displaySuccess, displayError } = useDisplayFeedback();

	const path = `${rest_namespace}/import-settings/`;

	return useCallback(
		async ({ setInProgress, licence_key }) => {
			setInProgress(true);

			try {
				await fetchAPI.POST({ path, data: { licence_key } });
				displaySuccess({ title: __('Settings imported successfully') });
				window.location.reload();
			} catch (error) {
				console.log('ERROR', error);
				displayError({ title: __('Error importing settings') });
			} finally {
				setInProgress(false);
			}
		},
		[displayError, displaySuccess, path]
	);
};
