import { Divider, Text } from '@wp-plugins/adapters';
import { __ } from '@wp-plugins/i18n';
import { SectionCard } from '@wp-plugins/components';
import { useFieldArray } from '@wp-plugins/form';

import { ReactionButtons } from './ReactionButtons';
import { UrlButtons } from './UrlButtons';
import type { DataShape } from '../../../services';
import { PREFIX } from '../constants';

const arrayName = `${PREFIX}.buttons` as const;

export const KeyboardButtons: React.FC = () => {
	const buttonsArray = useFieldArray<DataShape, `${typeof PREFIX}.buttons`, 'btnId'>({
		name: arrayName,
		keyName: 'btnId',
	});

	return (
		<SectionCard title={__('Inline Keyboard Buttons')} mt='1em'>
			<Text>{__('Here you can add your own custom buttons.')}</Text>
			<Divider />
			<ReactionButtons buttonsArray={buttonsArray} />
			<Divider />
			<UrlButtons buttonsArray={buttonsArray} />
		</SectionCard>
	);
};
