import { useCallback } from 'react';
import { findIndex, propEq } from 'ramda';

import { __ } from '@wp-plugins/i18n';
import { CheckIcon } from '@wp-plugins/icons';
import { Flex, IconButton } from '@wp-plugins/adapters';
import { uuid } from '@wp-plugins/utilities';

import ButtonInput from './ButtonInput';
import type { ButtonInputProps } from './types';

const getInputs = () => [
	{
		name: 'label',
		placeholder: __('Label') + ' ',
		style: { width: '45%' },
	},
	{
		name: 'url',
		placeholder: '{post_url}?utm_source=telegram',
	},
];

export const UrlButtonsInput: React.FC<ButtonInputProps> = ({
	fields,
	append,
	update,
	defaultInputValues,
	btnInputValues,
	setBtnInputValues,
}) => {
	const submit = useCallback(() => {
		const isNew = !('id' in btnInputValues);

		const label = btnInputValues.label.trim();
		const url = btnInputValues.url.trim();

		if (isNew) {
			append({ id: uuid(), ...btnInputValues, url, label });
		} else {
			const index = findIndex(propEq<string>('id', btnInputValues.id), fields);
			if (index < 0) {
				append({ ...btnInputValues, url, label });
			} else {
				update(index, { ...btnInputValues, url, label });
			}
		}
		setBtnInputValues(defaultInputValues);
	}, [append, btnInputValues, defaultInputValues, fields, setBtnInputValues, update]);

	const isDisabled = !(btnInputValues.label && btnInputValues.url);

	const onChangeValue = useCallback(
		(name: string) => (value: string) => {
			setBtnInputValues((prevState) => ({
				...prevState,
				[name]: value,
			}));
		},
		[setBtnInputValues]
	);

	return (
		<Flex maxWidth='700px' m='0 auto' my='1em'>
			{getInputs().map((input) => {
				const { name, style, ...rest } = input;
				return (
					<ButtonInput
						key={name}
						value={btnInputValues[name]}
						onPressEnter={!isDisabled && submit}
						onChangeValue={onChangeValue(name)}
						style={style}
						{...rest}
					/>
				);
			})}
			<IconButton
				isDisabled={isDisabled}
				onClick={submit}
				icon={<CheckIcon />}
				borderRadius={0}
				aria-label={__('Done')}
				minW='2.5rem'
			/>
		</Flex>
	);
};
