import { assocPath, objOf, pathOr, prop, has } from 'ramda';

import { sprintf, __ } from '@wp-plugins/i18n';

import type { DataShape } from './types';

export const processData =
	(predicate: any) =>
	(data: DataShape): DataShape => {
		// adjust `value` prop in repeatable fields
		const botsCollectionPath = ['bots', 'collection'];
		const botsCollection = pathOr([], botsCollectionPath, data).map(predicate);
		let normalizedData = assocPath(botsCollectionPath, botsCollection, data);

		const catchEmailsPath = ['notify', 'catch_emails'];
		const catchEmails = pathOr([], catchEmailsPath, normalizedData)
			.map((catchEmail) => {
				const chatIdsPath = has('value', catchEmail) ? ['value', 'chat_ids'] : ['chat_ids'];

				const chatIds = pathOr([], chatIdsPath, catchEmail).map(predicate);

				return assocPath(chatIdsPath, chatIds, catchEmail);
			})
			.map(predicate);

		normalizedData = assocPath(catchEmailsPath, catchEmails, normalizedData);

		return normalizedData;
	};

/**
 * Prepare default values from REST API schema
 */
export const prepDefaultValues = processData(objOf('value'));

/**
 * Normalizes form data for submission as per the REST schema
 */
export const normalizeData = processData(prop('value'));

export const getLicenceErrorMessage = (error: string, result?: unknown): string => {
	let message: string;
	switch (error) {
		case 'expired':
			message = sprintf(__('Your license key expired on %s.'), (result as any)?.expires);
			break;
		case 'disabled':
		case 'revoked':
			message = __('Your license key has been disabled.');
			break;
		case 'missing':
		case 'invalid':
			message = __('Invalid license key.');
			break;
		case 'site_inactive':
			message = __('Your license is not active for this website.');
			break;
		case 'item_name_mismatch':
			message = __('This appears to be an invalid license key for this product.');
			break;
		case 'no_activations_left':
			message = __('Your license key has reached its activation limit.');
			break;
		default:
			message = __('An error occurred, please try again.');
			break;
	}
	return message;
};
