import { useCallback } from 'react';

import { Input, InputProps } from '@wp-plugins/adapters';

interface ButtonInputProps extends InputProps {
	onPressEnter?: VoidFunction;
	onChangeValue?: (value: string) => void;
}

const ButtonInput: React.FC<ButtonInputProps> = ({ onChangeValue, onPressEnter, value = '', ...rest }) => {
	const onKeyPress = useCallback(
		(event) => {
			if (event.key === 'Enter') {
				event.preventDefault();
				if (onPressEnter) {
					onPressEnter();
				}
			}
		},
		[onPressEnter]
	);

	const onChange = useCallback<InputProps['onChange']>(
		({ nativeEvent: e }) => {
			onChangeValue?.((e.target as HTMLInputElement).value);
		},
		[onChangeValue]
	);

	return (
		<Input
			onKeyPress={onKeyPress}
			onChange={onChange}
			minWidth='70px'
			autoComplete='off'
			borderRadius={0}
			value={value}
			{...rest}
		/>
	);
};

export default ButtonInput;
