import { useCallback } from 'react';
import { find, pathEq } from 'ramda';

import { useBotCollection } from './useBotCollection';

type Callback = (bot_username: string) => string;

export const useBotToken = (): Callback => {
	const collection = useBotCollection();

	return useCallback<Callback>(
		(bot_username) => {
			return find(pathEq(['value', 'bot_username'], bot_username), collection)?.value?.bot_token;
		},
		[collection]
	);
};
