import { useState, useCallback } from 'react';

import { __ } from '@wp-plugins/i18n';
import { Button, Divider, Text, Flex, Box } from '@wp-plugins/adapters';
import {
	checkWebhookInfo,
	deleteWebhook,
	ResultType,
	setWebhook,
	WebhookStatus,
	WebhookUtilArgs,
} from '@wp-plugins/services';

import { useData } from '../../services';

interface WebhookInfoPorps {
	bot_token: string;
	hasError: boolean;
}

const prepareWebhookUrl = (webhook_url: string) => (bot_token: string) => webhook_url.replace('%bot_token%', bot_token);

export const WebhookInfo: React.FC<WebhookInfoPorps> = ({ bot_token, hasError }) => {
	// status can be set, notSet, error etc.
	const [checkingWebhookInfo, setCheckingWebhookInfo] = useState(false);
	const [hasCheckedWebhookInfo, setHasCheckedWebhookInfo] = useState(false);
	const [webhookInfoResult, setWebhookInfoResult] = useState(__('Please make sure Webhook is set.'));
	const [webhookInfoResultType, setWebhookInfoResultType] = useState<ResultType>('ERROR');

	const [doingWebhookAction, setDoingWebhookAction] = useState(false);

	const [webhookActionResult, setWebhookActionResult] = useState('');
	const [webhookActionResultType, setWebhookActionResultType] = useState('');

	const [webhookStatus, setWebhookStatus] = useState<WebhookStatus>(null);

	const webhookActionButtonText = 'SET' === webhookStatus ? __('Delete Webhook') : __('Set Webhook');

	const webhook_url = useData('uiData')?.webhook_url;
	const getWebhookUrl = prepareWebhookUrl(webhook_url);

	const setStatus = useCallback<WebhookUtilArgs['setStatus']>(
		(status, result) => {
			if ('SET' === status) {
				setWebhookInfoResult(__('Webhook is set.'));
				setWebhookInfoResultType('SUCCESS');
			} else if ('NOT_SET' === status) {
				setWebhookInfoResult(__('Webhook is not set.'));
				setWebhookInfoResultType('ERROR');
			} else if (result) {
				setWebhookInfoResult(result());
				setWebhookInfoResultType('ERROR');
			}
			setHasCheckedWebhookInfo(true);
			setWebhookStatus(status);
		},
		[setWebhookInfoResult, setWebhookInfoResultType, setHasCheckedWebhookInfo, setWebhookStatus]
	);

	const onClickGetInfo = useCallback(
		async (event) =>
			await checkWebhookInfo(
				{
					bot_token,
					setInProgress: setCheckingWebhookInfo,
					setStatus,
					url: getWebhookUrl(bot_token),
				},
				event
			),
		[bot_token, getWebhookUrl, setStatus]
	);

	const onClickWebhookAction = useCallback(
		async (event) => {
			const webhookAction = 'SET' === webhookStatus ? deleteWebhook : setWebhook;
			await webhookAction(
				{
					bot_token,
					setInProgress: setDoingWebhookAction,
					setResult: setWebhookActionResult,
					setResultType: setWebhookActionResultType,
					setStatus,
					url: getWebhookUrl(bot_token),
					allowed_updates: '[]',
				},
				event
			);
		},
		[bot_token, getWebhookUrl, setStatus, webhookStatus]
	);

	return (
		<>
			<Flex alignItems='center' flexWrap='wrap'>
				<Box mx='0.5em'>
					<Text fontWeight='bold'>{__('Webhook Info')}</Text>
				</Box>
				<Box mx='0.5em'>
					<Button
						color='blue.500'
						size='sm'
						isDisabled={checkingWebhookInfo || hasError}
						onClick={onClickGetInfo}
					>
						{checkingWebhookInfo ? __('Please wait') : __('Get Info')}
					</Button>
				</Box>
				<Box mx='0.5em'>
					{webhookInfoResult ? (
						<Text fontWeight='bold' color={webhookInfoResultType === 'ERROR' ? 'red.400' : 'green.500'}>
							{webhookInfoResult}
						</Text>
					) : null}
				</Box>
			</Flex>

			{webhookInfoResult && hasCheckedWebhookInfo ? (
				<>
					<Divider my='0.5em' />
					<Flex alignItems='center' flexWrap='wrap'>
						<Box mx='0.5em'>
							<Text fontWeight='bold'>{__('Action')}</Text>
						</Box>
						<Box mx='0.5em'>
							<Button
								color={'SET' === webhookStatus ? 'red.400' : 'gray.500'}
								size='sm'
								isDisabled={doingWebhookAction || hasError}
								onClick={onClickWebhookAction}
							>
								{doingWebhookAction ? __('Please wait') : webhookActionButtonText}
							</Button>
						</Box>
						<Box mx='0.5em'>
							{webhookActionResult ? (
								<Text
									fontWeight='bold'
									color={webhookActionResultType === 'ERROR' ? 'red.400' : 'green.500'}
								>
									{webhookActionResult}
								</Text>
							) : null}
						</Box>
					</Flex>
				</>
			) : null}
		</>
	);
};
