import { useCallback } from 'react';

import type { SubmitHandler, UseFormReturn } from '@wp-plugins/form';
import { useSubmitForm } from '@wp-plugins/services';

import type { DataShape } from './types';
import { useData } from './useData';
import { getErrorMessage } from './fields';
import { normalizeData, prepDefaultValues } from './utils';

export const useOnSubmit = (form: UseFormReturn<DataShape>): SubmitHandler<DataShape> => {
	const { rest_namespace } = useData('api');

	const path = `${rest_namespace}/settings/`;

	const onSubmitSuccess = useCallback(
		(data) => {
			// Reload the page if the p2tg is activated or deactivated
			if (form.formState.defaultValues?.p2tg?.active !== data?.p2tg?.active) {
				window.location.reload();
			}
		},
		[form]
	);

	const submitForm = useSubmitForm<DataShape>({
		form,
		path,
		getErrorMessage: getErrorMessage as any,
		normalizeData,
		prepDefaultValues,
		resetForm: true,
		onSubmitSuccess,
	});

	return useCallback(
		async (data) => {
			await submitForm(data);
		},
		[submitForm]
	);
};
