import { useMemo } from 'react';

import { __ } from '@wp-plugins/i18n';
import type { FormFieldProps } from '@wp-plugins/form';
import { useChatWithTest } from '@wp-plugins/components';
import type { TestResult, TestResultType } from '@wp-plugins/services';

import { getFieldLabel, useData, useBotToken } from '../../services';

type UseCatchEmailsFields = {
	fields: FormFieldProps<any, any>['subFields'];
	testResult: TestResult;
	testResultType: TestResultType;
};

export const useCatchEmailsFields = (botUsername: string): UseCatchEmailsFields => {
	const admin_email = useData('uiData')?.admin_email;

	const bot_token = useBotToken()(botUsername);

	const { button, result, testResult, testResultType } = useChatWithTest(bot_token, false);

	return useMemo(
		() => ({
			fields: [
				{
					name: 'email',
					fieldType: 'text',
					label: getFieldLabel('email_goes_to'),
					placeholder: admin_email,
				},
				{
					name: 'chat_ids',
					fieldType: 'text.button',
					label: getFieldLabel('send_to_chat'),
					button,
					description: __('Telegram User or Group Chat ID.'),
					isRepeatable: true,
					after: () => result,
					placeholder: '98765432',
				},
			],
			testResult,
			testResultType,
		}),
		[admin_email, button, result, testResult, testResultType]
	);
};
