import { Text } from '@wp-plugins/adapters';
import { __ } from '@wp-plugins/i18n';
import type { ResultType } from '@wp-plugins/services';

import { getLicenceErrorMessage } from '../../../services';

type ActivationResultProps = {
	activationResult: any;
	activationResultType: ResultType;
};

const ActivationResult: React.FC<ActivationResultProps> = ({ activationResult, activationResultType }) => {
	let result: string;
	let resultType = activationResultType;
	// if request was successful
	if ('SUCCESS' === activationResultType) {
		if (false === activationResult?.success) {
			resultType = 'ERROR';
			result = getLicenceErrorMessage(activationResult?.error, activationResult);
		} else if (true === activationResult?.success && 'valid' === activationResult?.license) {
			result = __('License key has been activated successfully.') + ' ' + __("Don't forget to save the changes.");
		}
	} else if ('ERROR' === activationResultType) {
		result = activationResult;
	}
	return result ? (
		<Text fontWeight='bold' color={resultType === 'ERROR' ? 'red.400' : 'green.500'}>
			{result}
		</Text>
	) : null;
};

export default ActivationResult;
