import { useEffect } from 'react';

import { IconButton, useDisclosure } from '@wp-plugins/adapters';
import { EditIcon } from '@wp-plugins/icons';
import { __ } from '@wp-plugins/i18n';
import { FormField, useWatch } from '@wp-plugins/form';
import { isLocalUrl } from '@wp-plugins/utilities';

import { useData, getFieldLabel, DataShape } from '../../../services';
import { Modal } from './Modal';
import { useLicenceInfo } from './useLicenceInfo';
import { PREFIX } from '../constants';

import './style.scss';

const allowed_status = ['valid', 'expired'];

const Licence: React.FC = () => {
	const initialKey = useData('savedSettings')?.advanced?.licence_key;

	const licence_key = useWatch<DataShape, `${typeof PREFIX}.licence_key`>({ name: `${PREFIX}.licence_key` as const });
	const licenceInfo = useLicenceInfo(licence_key);

	const { isOpen, onClose, onOpen } = useDisclosure();

	const isError =
		// if it's not localhost
		!isLocalUrl() &&
		// if the actual request did not err
		'ERROR' !== licenceInfo?.requestStatus &&
		// if there is an error message
		licenceInfo?.error &&
		// if it's not expired or valid
		!allowed_status.includes(licenceInfo?.status);

	useEffect(() => {
		const timeout = setTimeout(() => {
			if (isError) {
				onOpen();
			}
		}, 3000);

		return () => {
			clearTimeout(timeout);
		};
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [isError]);

	let title: string;
	switch (true) {
		case !initialKey:
			title = __('Lets activate the licence.');
			break;
		case isError:
			title = __('There seems to be some issue with your licence.');
			break;
		default:
			title = __('Manage your licence.');
			break;
	}

	const button = <IconButton aria-label={__('Edit licence')} variant='ghost' icon={<EditIcon />} onClick={onOpen} />;

	return (
		<>
			<FormField
				name={`${PREFIX}.licence_key`}
				fieldType='text.button'
				label={getFieldLabel('licence_key')}
				maxWidth='200px'
				background='#f5f5f5'
				addonAfter={button}
			/>
			{isOpen && <Modal isOpen={true} onClose={onClose} licenceInfo={licenceInfo} title={title} />}
		</>
	);
};

export default Licence;
