import { useWatch } from '@wp-plugins/form';
import { Collapse, Text } from '@wp-plugins/adapters';
import { __ } from '@wp-plugins/i18n';

export type IfBotProps = {
	name: string;
} & ({ children: (bot: string) => React.ReactNode } | { children: React.ReactNode });

export const IfBot: React.FC<IfBotProps> = ({ children, name }) => {
	const bot = useWatch({ name });

	const hasBot = Boolean(bot);

	return (
		<>
			{!hasBot && <Text color='red.500'>{__('You must choose a bot for the module.')}</Text>}

			<Collapse in={hasBot} animateOpacity>
				{'function' === typeof children ? children(bot) : children}
			</Collapse>
		</>
	);
};
